.about-us {
  z-index: 1;
  position: relative;

  height: max-content;
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  background-size: cover;
  background-image: url("../images/block1a.jpg");
  background-repeat: no-repeat;
  @include shadow-block;

  &__strip {
    margin-top: 50px;
    margin-bottom: 67px;

    @include strip;
  }

  &__title {
    margin-bottom: 47px;

    @include about-us-title;
  }

  &__p {
    max-width: 950px;
    padding: 0 50px 70px;

    @include about-us-p;
  
    text-align: left;
    color: #e5e5e5;

    white-space: normal;
  }

  &__read-more {
    width: 270px;
    height: 75px;

    font-family: "Gill Sans", sans-serif;
    font-size: 30px;
    font-style: italic;
    font-weight: lighter;
    text-transform: uppercase;

    text-align: center;
    line-height: 75px;
    color: #e5e5e5;
    border: solid 2px #064d66;

    transition: 0.3s linear;

    &:hover {
      background-color: #064d66;
      color: #e5e5e5;
    }
  }
}

