.nav {
  -webkit-user-select: none;
  text-decoration: none;

  &__items {
    display: grid;
    grid-template-columns: repeat(6, max-content);

    @include nav-text;

    :hover {
      text-decoration: none;
    }
  }

  &__link {
    color: #fff;
  }

  &__list {
    padding: 27.5px 20px;

    transition: 0.35s linear;

    &:hover {
      background-color: rgba($color: $c-gray, $alpha: 0.87);
      color: #0b6282;
    }
  }
}

@media (max-width: 950px) {
  .nav {
    display: none;
  }
}
